/** Media Queries **/
html {
  font-size: 62.5%;
  box-sizing: border-box;
  background-color: #e4e5f1;
}
html.dark {
  background-color: #161722;
}
html.dark .attribution {
  color: white;
}
html.dark header {
  background-image: url("../images/bg-mobile-dark.jpg");
}
@media (min-width: 1440px) {
  html.dark header {
    background-image: url("../images/bg-desktop-dark.jpg");
  }
}
html.dark .todo__input {
  background-color: #25273c;
}
html.dark .todo__input input[type=text] {
  background-color: #25273c;
  color: white;
}
html.dark .todo__container ul li {
  border-bottom: 2px solid #777a92;
  background-color: #25273c;
}
html.dark .todo__container ul li p {
  color: white;
}
html.dark .todo__container ul li:last-of-type {
  border-bottom: none;
}
html.dark .todo__container ul li:last-of-type span {
  color: white;
}
html.dark .todo__container .non-sortable .ul-footer {
  background-color: #25273c;
}
html.dark .controls {
  background-color: #25273c;
}
html.dark .controls span {
  background-color: #25273c;
  color: white;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Josefin Sans", sans-serif;
  font-size: 1.6rem;
}

.container {
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
}

button {
  border: none;
}

.error {
  padding: 2rem;
  text-align: center;
  color: white;
  font-size: 2rem;
  background-color: #e97070;
  margin: 1rem;
  opacity: 0;
  transition: all 1s ease;
}

.opac {
  opacity: 1 !important;
}

.low-error {
  background-color: #619dce;
}

.attribution {
  font-size: 11px;
  text-align: center;
}
.attribution a {
  color: #3e52a3;
}

.line-through {
  text-decoration: line-through;
  color: red;
}

.display-none {
  display: none !important;
}

.selectedFooter {
  color: #3a7bfd !important;
}

header {
  background-image: url("../images/bg-mobile-light.jpg");
  background-position: center center;
  background-size: cover;
  width: 100%;
  margin: 0;
  padding: 2rem;
  padding-top: 3rem;
  padding-bottom: 9rem;
}
@media (min-width: 1440px) {
  header {
    background-image: url("../images/bg-desktop-light.jpg");
    background-position: center center;
    background-size: cover;
    padding-left: 20rem;
    padding-right: 20rem;
  }
}

nav {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  padding-bottom: 2rem;
}
@media (min-width: 1440px) {
  nav {
    justify-content: space-around;
  }
  nav h1 {
    font-size: 5rem;
  }
}
nav h1 {
  margin: 0;
  color: #fafafa;
  font-weight: 700;
  padding: 1rem;
  font-size: 3.4rem;
  text-transform: uppercase;
}

.container {
  display: grid;
}
@media (min-width: 1440px) {
  .container {
    width: 120rem;
  }
}

.todo__input {
  display: flex;
  flex-direction: row;
  align-items: center;
  padding: 1rem;
  padding-left: 2.5rem;
  padding-right: 2.5rem;
  border-radius: 1rem;
  background-color: #fafafa;
  transform: translateY(-8rem);
  width: auto;
}
@media (min-width: 1440px) {
  .todo__input {
    width: auto;
  }
}
.todo__input input[type=text] {
  border: none;
  background-color: #fafafa;
  padding: 1rem;
  padding-right: 3rem;
  margin-right: 2rem;
  width: 27vh;
}

.radio {
  display: flex;
  align-items: center;
  justify-content: center;
  justify-items: center;
  width: 2rem;
  height: 2rem;
  cursor: pointer;
  border-radius: 50%;
  border: 0.01rem solid #9394a5;
}
.radio .radio__radio {
  width: 2rem;
  height: 2rem;
  border-radius: 50%;
  border: 0.01rem solid #9394a5;
  display: block;
  background: linear-gradient(#57ddff, #c058f3);
  border-radius: 50%;
  transform: scale(0);
  transition: transform 0.15s;
}

.active {
  transform: scale(1) !important;
}

.todo__container {
  transform: translateY(-7rem);
  display: grid;
  grid-template-columns: 1fr;
  width: auto;
}
@media (min-width: 1440px) {
  .todo__container {
    width: 80rem;
  }
}
.todo__container ul {
  box-shadow: 0px 10px 8px 9px rgba(0, 0, 0, 0.05);
  list-style: none;
  padding: 0;
  margin: 0;
}
.todo__container ul li {
  display: grid;
  align-items: center;
  grid-template-columns: 7.4% 80% 12.6%;
  gap: 1rem;
  background-color: #fafafa;
  padding: 1.6rem;
  border-bottom: 2px solid #e4e5f1;
}
.todo__container ul li:first-of-type {
  border-top-left-radius: 1rem;
  border-top-right-radius: 1rem;
}
.todo__container ul li p {
  font-size: 1.3rem;
  font-weight: 400;
  color: #484b6a;
  padding: 0;
  margin: 0;
  overflow-x: scroll;
}
@media (min-width: 1440px) {
  .todo__container ul li p {
    overflow-x: hidden;
  }
}
.todo__container span {
  margin: 0;
  padding: 0;
  color: #484b6a;
  text-align: center;
  margin-top: 5rem;
  cursor: pointer;
}

.controls {
  padding: 1rem;
  margin-top: 1rem;
  background-color: #fafafa;
  display: flex;
  flex-direction: row;
  justify-content: center;
  border-radius: 1rem;
  box-shadow: 0px 0px 8px 9px rgba(0, 0, 0, 0.05);
}
.controls span {
  border: none;
  background-color: #fafafa;
  margin: 1rem;
  padding: 0;
  color: #484b6a;
}
.controls span.selected {
  color: #3a7bfd;
}

.non-sortable {
  box-shadow: 0px 10px 8px 9px rgba(0, 0, 0, 0.05);
  list-style: none;
  padding: 0;
  margin: 0;
}
.non-sortable .ul-footer {
  display: grid;
  align-items: center;
  grid-template-columns: 7.4% 80% 12.6%;
  gap: 1rem;
  background-color: #fafafa;
  padding: 1.6rem;
  width: auto;
  border-bottom: 2px solid #e4e5f1;
  border-bottom-left-radius: 1rem;
  border-bottom-right-radius: 1rem;
  border-bottom: none;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}
.non-sortable .ul-footer span {
  font-size: 1.2rem;
  margin: 0;
  padding: 0;
}
.non-sortable .ul-footer span:hover {
  color: #3a7bfd;
}